.transaction-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 18px;
  width: 280px;
}

/* Ascunde sgeile de incrementare/decrementare din cmpul numr */
.input[type='number']::-webkit-inner-spin-button,
.input[type='number']::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.input,
.select,
.textarea {
  color: rgba(255, 255, 255, 0.6);
  width: 280px;
  padding: 8px;
  border: none;
  border-bottom: 1px solid rgba(255, 255, 255, 0.4);
  background-color: transparent;
  font-size: 18px;
  font-weight: 600;
  resize: none;
  margin-bottom: 30px;
}

.input::placeholder,
.select::placeholder,
.textarea::placeholder {
  color: rgba(255, 255, 255, 0.6);
}

.input:focus,
.select:focus,
.textarea:focus {
  outline: none;
}

.select option {
  background-color: #734aef;
  color: white;
}

/* Schimb culoarea iconiei calendarului pentru browserele WebKit */
.input[type='date']::-webkit-calendar-picker-indicator {
  filter: invert(75%) sepia(100%) saturate(340%) hue-rotate(256deg)
    brightness(103%) contrast(90%);
}

.toggle-button,
.submit-button {
  padding: 8px 16px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-bottom: 20px;
}

.submit-button {
  background-color: #28a745;
  color: white;
}

@media only screen and (min-width: 768px) {
  .form-row {
    display: flex;
    gap: 2rem;
    align-items: center;
    align-content: center;
  }

  .select {
    cursor: pointer;
  }

  .input {
    width: 181px;
  }

  .amount-style {
    text-align: center;
  }

  .select,
  .textarea {
    width: 394px;
  }
}
